import React from "react";
import styled from "styled-components";

export const IconWithTextBase = ({className, children}) => {
    const icon = children.at(0)
    const newChildren = React.Children.toArray(children).slice(1)
    return (
        <div className={className}>
            {icon}
            <div>
                {newChildren}
            </div>
        </div>
    )
}

export const IconWithText = styled(IconWithTextBase)`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  @media(max-width: 700px) {
    min-height: 140px;
  }
`