import styled from "styled-components";
import React from "react";

const LeftContainerBase = ({className, children, customRef}) => {
    return (
        <div className={className} ref={customRef}>
            {children}
        </div>
    )
}

export const LeftContainer = styled(LeftContainerBase)`
  display: flex;
  flex-direction: column;
  position: absolute;
  left: ${props => props.theme.leftContainerLeft};
  top: ${props => props.theme.leftContainerTop};
  width: ${props => props.theme.titleWidth};
  
  @media(max-width: ${props => props.theme.leftContainerChange}) {
    position: static;
    margin-bottom: ${props => props.theme.leftContainerPaddingBottomMobile};
  }
  
  @media(max-width: ${props => props.theme.leftContainerChange2}) {
    top: 0;
    left: ${props => props.theme.logoPadding};
  }

  @media(max-width: 470px) { // bylo 700px
    left: ${props => props.theme.logoPaddingMobile};
  }
`