import React from "react";
import styled from "styled-components";

const StyledSeparatedList = styled.div`
  display: grid;
  align-content: start;
  
  @media(max-width: 700px) {
     grid-template-rows: repeat(2, 1fr 21px) 1fr;
     grid-template-columns: 1fr;
     padding: 0 33px;  
  }
`

const SeparatingLine = styled.div`
  background: #CECDCD;
  width: 100%;
  height: 1px;
  //left: 101px;
  //top: 788px;

  @media(max-width: 700px) {
    margin: 10px 0;
    min-height: 0;
  }
`

export const SeparatedList = ({children, separator = SeparatingLine}) => {
    const Separator = separator;
    return (
        <StyledSeparatedList>
            {children &&
                React.Children.map(children, (child, index) =>
                <React.Fragment key={index}>
                    {child}
                    {index < React.Children.count(children) - 1 && <Separator/>}
                </React.Fragment>
                )}
        </StyledSeparatedList>
    )
}