import React from "react";
import {convincingProperties} from "../Orange/ConvincingBox/ConvincingProperties";
import {ConvincingItem} from "../components/Front/ConvincingBox/ConvincingItem";
import {ConvincingBox} from "../components/Front/ConvincingBox/ConvincingBox";
import {StyledIconItem} from "../components/Shared/StyledIconItem";

export const PurpleConvincingBox = () => {
    const items = convincingProperties.map((item, index) =>
        <ConvincingItem>
            <StyledIconItem>{React.createElement(item.icon)}</StyledIconItem>
            {item.title}
            {item.text}
        </ConvincingItem>
    )

    return (
        <ConvincingBox>
            {items}
        </ConvincingBox>
    )
}