import React from 'react';
import ReactDOM from 'react-dom';
import Orange from "./Orange/Orange";
import OrangeCart from "./Orange/OrangeCart";
import Purple from "./Purple/Purple";
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import {Link} from "react-router-dom";

ReactDOM.render((
    <BrowserRouter>
        <Switch>
            <Route exact path='/orange' component={Orange}/>
            <Route path='/orange/cart' render={(props) => <OrangeCart photoKey={(new URLSearchParams(props.history.location.search)).get('key')} />}/>
            <Route exact path='/purple' component={Purple}/>
        </Switch>
        <Link to={'/orange'}>Orange</Link>
        <br/>
        <Link to={'/orange/cart'}>Orange/Cart</Link>
        <br/>
        <Link to={'/purple'}>Purple</Link>
    </BrowserRouter>
    ), document.getElementById('root')
);

