import styled from "styled-components";
import {Button} from "./Button";
import React from "react";
import axios from "axios";

const HiddenInput = styled.input`
    display: none;
`

export const UploadButtonBase = ({children, className, onSuccess = undefined, onError = undefined}) => {
    let inputElement = undefined;
    const handleSelect = () => {
        if (inputElement)
            inputElement.click();
    }

    const handleFileChange = () => {
        if (inputElement.files.length > 0) {
            let form = new FormData();
            form.append("UploadForm[documentId]", "374");
            form.append("UploadForm[language]", "PL");
            form.append("UploadForm[imageFile]", inputElement.files[0]);
            axios.post(
                'https://thingproxy.freeboard.io/fetch/https://test2.fotoexpress.biz/api/upload',
                form,
                {
                    headers : {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(({data}) => {
                if (data.status === 200) {
                    if (onSuccess)
                        onSuccess(data.photoKey);
                }
                else if (onError)
                    onError();
            }).catch(error => {
                    if (onError) {
                        if (error.response) {
                            onError({status: error.response.status, error: "Server error"});
                        } else
                            onError({status: 0, error: (error.request) ? error.request : error.message});
                    }
                }
            );
        }
    }

    return (
        <Button className={className} onClick={handleSelect}>
            <HiddenInput ref={input => {
                inputElement = input;
                if (input)
                    input.onchange = handleFileChange;
            }} type={"file"} accept={"image/jpeg"}/>
            {children}
        </Button>
    );
}

export const UploadButton = styled(UploadButtonBase)`
  background: ${props => props.theme.uploadButtonGradient};
  box-shadow: 0 18px 25px -6px rgba(244, 206, 194, 0.8);
  color: #FFFFFF;
  text-shadow: 0 1px 1px rgba(67, 51, 8, 0.100934);
`
