import {Background} from "../Shared/Background";
import {Message} from "./Message";
import {SeparatedList} from "./SeparatedList";
import React, {useEffect, useState} from "react";
import styled from "styled-components";
import {
    CheckStates,
    DefaultPickItem,
    ExpandablePickItem,
    InfoPickItem
} from "./PickBox/PickItem";
import {SummaryInfo} from "./PickBox/Summary";
import {PaymentInfo} from "./PickBox/PaymentInfo";
import {ExpandableBox} from "./PickBox/ExpandableBox";
import {ReactComponent as Camera} from "../../icons/cart-icons/camera.svg";
import {ReactComponent as Printer} from "../../icons/cart-icons/printer.svg";
import {ReactComponent as Truck} from "../../icons/cart-icons/truck.svg";
import {ReactComponent as Wand} from "../../icons/cart-icons/wand.svg";
import {ReactComponent as Viewfinder} from "../../icons/cart-icons/viewfinder.svg";
import {ReactComponent as Tick} from "../../icons/onlytick.svg";
import {RightContainer} from "./RightContainer/RightContainer";

const Container = styled.div`
  display: grid;
  grid-template-columns: 3fr 1.8fr;
  grid-template-rows: minmax(calc(100vh - ${props => props.theme.footerSize}), auto) ${props => props.theme.footerSize};
  grid-template-areas:
          "main side"
          "footer footer";
`;

const Main = styled.div`
  grid-area: main;
  position: relative;
`;

const InnerMain = styled.div`
  padding: 100px;
`;

const Side = styled.div`
  grid-area: side;
  background-color: white;
  padding-right: 40px;
`;

//TODO: move to shared space
const Footer = styled.div`
  grid-area: footer;
  background-color: #403F40;
`;

const DisabledCheckBox = styled.div`
  display: inline-block;
  line-height: 64px;
  background-color: #B9B8B8;//TODO: use parameter from style
  box-shadow: 0 5.86364px 14.6591px rgba(105, 105, 105, 0.2);
  border-radius: 7.81818px;//TODO: use parameter from style
  width:32px;
  height:32px;
  text-align: center;
  align-self: center;
  svg {
    height : 32px;//TODO: make more reasonable scaling, propably set size in the icon before passing to this component?
    vertical-align: super;
  }
`;

const CheckedCheckBox = styled.div`
  display: inline-block;
  line-height: 64px;
  background: ${props => props.theme.accentGradient};
  box-shadow: 0 5.86364px 14.6591px rgba(105, 105, 105, 0.2);
  border-radius: 7.81818px;//TODO: use parameter from style
  width:32px;
  height:32px;
  text-align: center;
  align-self: center;
  cursor: pointer;
  svg {
    height : 32px;//TODO: make more reasonable scaling, propably set size in the icon before passing to this component?
    vertical-align: super;
  }
`

const UnCheckedCheckBox = styled.div`
  display: inline-block;
  line-height: 64px;
  background-color: white;//TODO: use parameter from style
  box-shadow: 0 5.86364px 14.6591px rgba(105, 105, 105, 0.2);
  border: 2px solid #E2E1E1;//TODO: use parameter from style
  box-sizing: border-box;
  border-radius: 5px;//TODO: use parameter from style
  width:32px;
  height:32px;
  text-align: center;
  align-self: center;
  cursor: pointer;
`

const checkStyles = new CheckStates(
    <UnCheckedCheckBox/>,
    <CheckedCheckBox>
        <Tick/>
    </CheckedCheckBox>,
    <DisabledCheckBox>
        <Tick/>
    </DisabledCheckBox>
);

const StyledBackground = styled(Background)`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
`;

export const Cart = ({photoKey}) => {

    const [state, setState] = useState({
        message: {}
    });

    useEffect(() => {
        const encodedKey = encodeURIComponent(photoKey);
        fetch(`https://thingproxy.freeboard.io/fetch/https://test2.fotoexpress.biz/api/result/?photoKey=${encodedKey}`)
            .then((response) => response.json())
            .then((responseJson) => {
                if (responseJson.status === 200)
                    setState({ data : responseJson, message : {text:"Done!", secondaryText:"Get your photo now.", status: "Automatic check passed."} })
            })
            .catch((error) => {
                console.error(error);
            });
    }, [photoKey]);

    return (
    <Container>
            <Main>
                <StyledBackground/>
                <InnerMain>
                    <div>
                        <div>photos + price goes here</div>
                    </div>
                    <Message text={state.message.text} textSecondary={state.message.secondaryText} status={state.message.status}/>
                    <SeparatedList>
                        <DefaultPickItem
                            checkStates={checkStyles}
                            text={"Digital basic"}
                            icon={<Camera/>}
                            price={state.data ? `${state.data.price} ${state.data.currency}` : undefined}
                        />
                        <ExpandablePickItem
                            checkStates={checkStyles}
                            text={"Please sent paper photo to my address"}
                            icon={<Printer/>}
                            price={state.data ? `${state.data.price} ${state.data.currency}` : undefined}
                        >
                            <div>W kocu dziaaaaaaaaa!</div>
                        </ExpandablePickItem>
                        <InfoPickItem
                            text={"Free home delivery"}
                            icon={<Truck/>}
                            price={state.data ? `${state.data.price} ${state.data.currency}` : undefined}
                        />
                        <ExpandablePickItem
                            checkStates={checkStyles}
                            text={"Photo retouching"}
                            icon={<Wand/>}
                            price={state.data ? `${state.data.price} ${state.data.currency}` : undefined}
                        >
                            <ExpandableBox/>
                        </ExpandablePickItem>
                        <ExpandablePickItem
                            checkStates={checkStyles}
                            text={"Lorem ipsum dolor si dolor"}
                            icon={<Viewfinder/>}
                            price={state.data ? `${state.data.price} ${state.data.currency}` : undefined}
                        >
                            <div>Retouching Process</div>
                        </ExpandablePickItem>
                        <SummaryInfo/>
                        <PaymentInfo/>
                    </SeparatedList>
                </InnerMain>
            </Main>
        <Side>
            <RightContainer/>
        </Side>
        <Footer/>
    </Container>
    );
}