import React from "react";
import {convincingProperties} from "./ConvincingProperties";
import {ConvincingItem} from "../../components/Front/ConvincingBox/ConvincingItem";
import {ExtendedConvincingBox} from "../../components/Front/ConvincingBox/ExtendedConvincingBox";
import {IconItemCircle} from "../../components/Shared/IconItemCircle";

export const OrangeConvincingBox = () => {
    const items = convincingProperties.map((item, index) =>
        <ConvincingItem key={index}>
            <IconItemCircle>{React.createElement(item.icon)}</IconItemCircle>
            {item.title}
            {item.text}
        </ConvincingItem>
    )

    return (
        <ExtendedConvincingBox>
            {items}
        </ExtendedConvincingBox>
    )
}