import React from "react";
import styled from "styled-components";
import {TextBase} from "../../../MainStyle";
import {IconWithText} from "../../Shared/IconWithText";

const ItemText = styled.p`
  ${TextBase};
  font-family: Avenir Medium, sans-serif;
  font-weight: 800;
  font-size: 15px;
  line-height: 35px;
  color: ${props => props.theme.accentColor};
  overflow-wrap: anywhere;

  @media(max-width: 700px) { // TODO bylo 700px ale brzydko wylazi tekst juz wczesniej
    font-size: 13px;
  }
`

const ItemTitleText = styled(ItemText)`
  font-size: 20px;
  color: ${props => props.theme.black};

  @media(max-width: 700px) { // TODO bylo 700px ale brzydko wylazi tekst juz wczesniej 
    font-size: 17px;
  }
`

export const ConvincingItem = ({children}) => {
    const icon = children.at(0)
    const title = children.at(1)
    const text = children.at(2)
    return (
        <IconWithText>
            {icon}
            <ItemTitleText>{title}</ItemTitleText>
            <ItemText>{text}</ItemText>
        </IconWithText>
    )
}