import React from "react";
import styled from "styled-components";

import {howProperties} from "./HowProperties"
import {HowItem} from "./HowItem";
import {TextBase} from "../../../../MainStyle";

const StyledHowBox = styled.div`

`

const Title = styled.h1`
  ${TextBase};
  font-size: 38px;
  line-height: 39px;
  letter-spacing: -1.8px;
  margin-bottom: 30px;
  color: #2B2824;
`

export const HowBox = () => {
    const items = howProperties.map((item, index) =>
        <HowItem text={item.text} title={item.title} icon={React.createElement(item.icon)}/>
    )

    return (
        <StyledHowBox>
            <Title>How it works?</Title>
            {items}
        </StyledHowBox>
    )
}