import React from "react";
import styled from "styled-components";
import {AnchorPoint} from "../Shared/AnchorPoint";

export const Photographer = styled.img`
  width: ${props => props.theme.photographerWidth};
  margin-right: ${props => props.theme.photographerMarginRight}; // todo sparametryzowa
  margin-top: ${props => props.theme.photographerMarginTop};

  @media(max-width: 1600px) {
    width: 50%;
  }
  
  @media(max-width: ${props => props.theme.leftContainerChange}) {
    width: ${props => props.theme.photographerWidthMobile};
  }
`

const StyledPhotoContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  flex: 1;
`

export const PhotographerContainer = ({children, src}) => {
    const photographer = children.at(0)
    const restChildren = React.Children.toArray(children).slice(1)
    return (
        <StyledPhotoContainer>
            {photographer}
            <AnchorPoint>
                {restChildren}
            </AnchorPoint>
        </StyledPhotoContainer>
    )
}