import React from "react";
import styled from "styled-components";
import {TextBase} from "../../../MainStyle";

const ButtonBase = ({className, children, onClick= undefined}) => {
    return (
        <div className={className} onClick={onClick}>
            <ButtonText> {children} </ButtonText>
        </div>
    )
}

const ButtonText = styled.p`
  ${TextBase};
  font-size: 19px;
  line-height: 33px;
  
  @media(max-width: 470px) {  // bylo 700px
    font-size: 12.7239px;
    line-height: 22px;
  }
`

export const Button = styled(ButtonBase)`
  display: flex;
  align-items: center;
  justify-content: center;
  width: ${props => props.theme.buttonWidth};//TODO: check whyyyyyyyyyyyy
  height: ${props => props.theme.buttonHeight};
  cursor: pointer;
  border-radius: 30px;

   @media(max-width: 470px) {  // bylo 700px
     width: 100%;
     height: ${props => props.theme.buttonMobileHeight};
   }
`