import styled from "styled-components";
import {TextBase} from "../../MainStyle";
import {AnchorPoint} from "../../components/Shared/AnchorPoint";
import {ReactComponent as CrossesIcon} from "../../icons/cross-pattern.svg";

const IdImg = styled.img`
    width: 100%;
`

const GuaranteeIconContainer = styled.div`
  position: absolute;
  left: calc(${props => props.theme.guaranteeBoxWidth} - 30px);
  top: -25px;
  
  @media(max-width: 700px) {
    left: calc(${props => props.theme.guaranteeBoxWidthMobile} - 15px);
    top: -18px;
  }
`

const CrossPattern = styled(CrossesIcon)`
  position: absolute;
  right: -165px;
  top: 180px;
  z-index: -2;
  
  @media(max-width: 700px) {
    width: 130px;
    top: 30px;
    right: -95px;
  }
`


const TitleText = styled.p`
  ${TextBase};
  font-family: Avenir Black, sans-serif;
  font-size: 39px;
  line-height: 36px;
  letter-spacing: 0.3px;
  
  color: ${props => props.theme.guaranteeTextColor};
  margin-bottom: 25px;
  
  @media(max-width: 700px) {
    margin-bottom: 10px;

    font-size: 18px;
    line-height: 16px;
    letter-spacing: 0.123928px;
  }
`

const Text = styled.p`
  ${TextBase};
  font-family: Avenir Black, sans-serif;
  font-size: 20px;
  line-height: 27px;
  letter-spacing: -0.4px;
  
  color: ${props => props.theme.guaranteeTextColor};
  margin-bottom: 25px;
  
  @media(max-width: 700px) {
    margin-bottom: 10px;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: -0.165238px;
  }
`

const TestContainer = styled.div`
  position: absolute;
  right: max(${props => (parseInt(props.theme.guaranteeBoxWidth) + 60) + 'px'}, 25vw);
  top: calc(20vw - 500px);
  
  @media(max-width: 2200px) {
    top: -2vw;
  }
  
  @media(max-width: 700px) {
    right: ${props => (parseInt(props.theme.guaranteeBoxWidthMobile) + 40) + 'px'};
    top: calc(22vw - 45px);
  }
`


const BaseGuaranteeBox = ({className, children}) => {
    const title = children.at(0)
    const text = children.at(1)
    const img = children.at(2) // todo zabezpieczyc
    const icon = children.at(3)
    return (
        <TestContainer>
            <AnchorPoint>
                <GuaranteeIconContainer>
                    {icon}
                </GuaranteeIconContainer>
                <CrossPattern/>
                <div className={className}>
                    <TitleText>{title}</TitleText>
                    <Text>{text}</Text>
                    <IdImg src={img} alt={'id'} />
                </div>
            </AnchorPoint>
        </TestContainer>
    )
}

export const GuaranteeBox = styled(BaseGuaranteeBox)`
  width: ${props => props.theme.guaranteeBoxWidth};
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  padding-top: 30px;
  background-color: ${props => props.theme.white};
  //position: absolute;
  //right: 100px;

  box-shadow: 0 33.012px 82.5301px -6.60241px rgba(219, 218, 232, 0.8);
  border-radius: 24px;
  
  @media(max-width: 700px) {
    width: ${props => props.theme.guaranteeBoxWidthMobile};
    padding-right: 13px;
    padding-left: 14px;
    padding-bottom: 13px;
    padding-top: 18px;

    border-radius: 9.91428px;
  }
`

