import { useHistory } from 'react-router-dom';
import {LeftContainer} from "../../components/Front/LeftContainer/LeftContainer";
import {ButtonBoxRow} from "../../components/Front/LeftContainer/ButtonBoxRow";
import {UploadButton} from "../../components/Shared/Buttons/UploadButton";
import {RequirementsButton} from "../../components/Shared/Buttons/RequirementsButton";
import {AdvantagesList} from "../../components/Front/LeftContainer/AdvantagesList";
import {OrangeLeftContainerProperties} from "./OrangeLeftContainerProperties";
import {AnchorPoint} from "../../components/Shared/AnchorPoint";
import {ReactComponent as ArrowIcon} from "../../icons/arrow.svg";
import {TitleText} from "../../components/Front/LeftContainer/TitleText";

import styled from "styled-components";
import {LeftArrow} from "../LeftArrow";

const Arrow = styled(ArrowIcon)`
  position: absolute;
  width: ${props => props.theme.upperArrowWidth};
  fill: ${props => props.theme.white};
  transform: rotate(163.5deg);
  top: ${props => props.theme.upperArrowTop};
  left: ${props => props.theme.upperArrowLeft};
  z-index: -2;

  @media(max-width: ${props => props.theme.leftContainerChange}) {
    top: ${props => props.theme.upperArrowTop1300};
    left: ${props => props.theme.upperArrowLeft1300};
  }
  
  @media(max-width: 700px) {
    width: ${props => props.theme.upperArrow700Width};
    top: ${props => props.theme.upperArrow700Top};
    left: ${props => props.theme.upperArrow700Left};
  }

  @media(max-width: 470px) {  // bylo 700px
    width: ${props => props.theme.upperArrowMobileWidth};
    top: ${props => props.theme.upperArrowTopMobile};
    left: ${props => props.theme.upperArrowLeftMobile};
  }
`

export const OrangeLeftContainer = ({customRef}) => {
    const history = useHistory();

    const OnPhotoUpload = photoKey => {
        console.log('hereee');
        history.push(`/orange/cart?key=${photoKey}`);
    }
    return (
        <LeftContainer customRef={customRef}>
            <TitleText>{OrangeLeftContainerProperties.title}</TitleText>
            <AnchorPoint>
                <LeftArrow/>
                <Arrow/>
            </AnchorPoint>
            <AdvantagesList>
                {OrangeLeftContainerProperties.advantagesList}
            </AdvantagesList>
            <ButtonBoxRow>
                <UploadButton onSuccess={OnPhotoUpload} onError={error => console.log(error)}>{OrangeLeftContainerProperties.uploadButton}</UploadButton>
                <RequirementsButton>{OrangeLeftContainerProperties.requirementsButton}</RequirementsButton>
            </ButtonBoxRow>
        </LeftContainer>
    )
}